<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Pesquisar Usuário">
          <v-card class="elevation-0">
            <v-card-text>
              <v-form>
                <v-layout
                  row
                  wrap>
                  <v-flex md4>
                    <v-text-field
                      v-model="filtros.nome"
                      label="Usuário"
                    />
                  </v-flex>
                  <v-flex md4>
                    <v-text-field
                      v-model="filtros.email"
                      label="Email"
                    />
                  </v-flex>
                  <v-flex md4>
                    <v-text-field
                      v-model="filtros.login"
                      label="Login"
                    />
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-btn
                :loading="loadingBtn"
                color="primary"
                @click="pesquisar"
              >Pesquisar</v-btn>
            </v-card-actions>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card>
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Usuários</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical
            />
            <v-spacer />
            <v-btn
              small
              fab
              color="primary">
              <v-icon @click="newItem()">mdi-plus</v-icon>
            </v-btn>
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            :total-items="totalItems"
            :pagination.sync="pagination"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="headerCell"
              slot-scope="{ header }">
              <span
                class="subheading font-weight-light text--darken-3"
                v-text="header.text"
              />
            </template>
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5
            >
              <td>{{ item.nome }}</td>
              <td>{{ item.email }}</td>
              <td>{{ item.login }}</td>
              <td>{{ item.rolesLabel }}</td>
              <td>{{ item.lojasLabel }}</td>
              <td class="justify-end layout ma-2">
                <v-icon
                  class="mr-2"
                  color="blue"
                  title="Editar dados do usuário"
                  @click="editItem(item)"
                >mdi-pencil</v-icon>
                <v-icon
                  color="red"
                  title="Excluir usuário"
                  @click="openDialogDelete(item)"
                >mdi-delete</v-icon>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <template
        v-if="dialog"
      >
        <v-layout
          row
          justify-center>
          <v-dialog
            v-model="dialog"
            max-width="390"
          >
            <v-card>
              <v-card-title class="headline">Excluir Usuário</v-card-title>

              <v-card-text>
                Confirma a exclusão do usuário <span class="title">{{ item.login }}</span>? Essa operação não poderá ser desfeita.
              </v-card-text>

              <v-card-actions>
                <v-spacer />

                <v-btn
                  outline
                  style="color: black !important"
                  @click="closeDialogDelete"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="red"
                  @click="deleteItem(item.id)"
                >
                  Excluir
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
      </template>
      <core-btn-permissao
        v-if="flgPedirPermissao"
        :action="deleteItem"
        :close="closeDialogDelete"
        :data-form="item.id"
      />
    </v-layout>
  </v-container>
</template>

<script>
import { UsuarioBusiness, RoleBusiness } from '../../business'
import { ACAO_EDITAR, LOCALSTORAGE } from '../../constants'
export default {
  metaInfo: {
    titleTemplate: '%s - Usuários'
  },
  data () {
    return {
      flgPedirPermissao: false,
      dialog: false,
      item: null,
      loading: true,
      loadingBtn: false,
      totalItems: 0,
      pagination: {
        rowsPerPage: 10,
        sortBy: 'nome'
      },
      selected: [],
      filtros: {
        nome: '',
        email: '',
        login: '',
        role: ''
      },
      roles: [],
      headers: [
        {
          sortable: false,
          text: 'Nome',
          value: 'nome'
        },
        {
          sortable: false,
          text: 'Email',
          value: 'email'
        },
        {
          sortable: false,
          text: 'Login',
          value: 'login'
        },
        {
          sortable: false,
          text: 'Perfil de Acesso',
          value: 'role'
        },
        {
          sortable: false,
          text: 'Lojas',
          value: 'lojasLabel'
        },
        {
          sortable: false,
          text: ''
        }
      ],
      items: []
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  methods: {
    reload () {
      this.paginar()
      RoleBusiness.findAll().then(response => { this.roles = response.data })
    },
    newItem () {
      this.$router.push('cad-user')
    },
    editItem (item) {
      this.$router.push(`cad-user/${item.id}/${ACAO_EDITAR}`)
    },
    openDialogDelete (item) {
      if (item.login === localStorage.getItem(LOCALSTORAGE.LOGIN)) {
        this.$root.showAlerta('Não é possível excluir o usuário que está logado')
        return
      }
      this.item = item
      this.dialog = true
    },
    closeDialogDelete () {
      this.item = null
      this.dialog = false
      this.flgPedirPermissao = false
    },
    deleteItem (id, token) {
      UsuarioBusiness.delete(id, token)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.callbackDelete()
        })
        .catch(error => {
          if (error.status === 405) { // METHOD_NOT_ALLOWED solicitar permissão
            this.flgPedirPermissao = true
          }
          this.$root.showErro(error.data)
        })
    },
    callbackDelete () {
      this.closeDialogDelete()
      this.reload()
    },
    pesquisar () {
      this.loadingBtn = true
      this.paginar()
    },
    paginar () {
      this.loading = true
      UsuarioBusiness.pagination(this.pagination.rowsPerPage, this.pagination.page - 1, this.pagination.sortBy || 'nome', this.filtros).then(response => {
        this.items = response.data.content
        this.totalItems = response.data.totalElements
      }).finally(() => {
        this.loadingBtn = false
        this.loading = false
      })
    }
  }
}
</script>
